.new-book-container {
    width: 100%;
    max-width: 1120px;
    height: 100vh;
    margin: 0 auto;

    display: flex;
    align-items: center;
    justify-content: space-between;
}

.new-book-container .content {
    width: 100%;
    padding: 96px;
    background: #F0F0F5;
    box-shadow: 0 0 100px rgba(0, 0, 0, 0.1);
    border-radius: 8px;

    display: flex;
    justify-content: space-between;
    align-items: center;

}

.new-book-container .content section {
    width: 100%;
    max-width: 380px;
}

.new-book-container section.form{
    width: 100%;
    max-width: 350px;
    margin-right: 30px;
}

.new-book-container section.form img {
    width: 45%;
    margin-left: auto;
    margin-right: auto;
    display: block;
}

.new-book-container .content section h1{
    margin: 64px 0 32px;
    font-size: 32px;
}

.new-book-container .content section p {
    font-size: 18px;
    color: #737380;
    line-height: 32px;
}

.new-book-container .content form {
    width: 100%;
    max-width: 450px;
}

.new-book-container .content form input {
    margin-top: 8px;
}

@media (max-width: 767px) {
    .new-book-container .content {
        padding: 24px;
    }

    .new-book-container section.form {
        margin-right: 0;
        margin-bottom: 24px;
    }

    .new-book-container section.form img {
        width: 70%;
        margin: 0 auto;
    }

    .new-book-container .content section h1 {
        margin: 16px 0 12px;
        font-size: 20px;
    }

    .new-book-container .content section p {
        font-size: 14px;
        line-height: 20px;
    }

    .new-book-container .content form {
        max-width: 300px;
    } 
}